body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body{
  color:#343d47;
  padding:0;
  margin:0;
  background-color: #f7f7f7;
  overflow-x:hidden;
}

.App > .content{
  padding: 0px 10px 10px 10px;
}

.link{
  text-decoration: none;
  font-style: italic;
  font-weight:bold;
  color: #001485;
}


.LegendDisplay{
    position: relative;
    border: 1px solid #ededed;
    display: inline-block;
    height: 500px;
    width: 200px;
    margin-top: 20px;
    overflow: scroll;
}

.legendTitle{
    font-weight: 700;
    margin: 0;
    padding: 0px;
    text-align: center;
}

.countryNameList{
    overflow-y: scroll;
    max-height: 200px;
    padding: 3px;
    cursor: pointer;
    width: 200px;
    margin: 2px;
    line-height: 12px;
    text-overflow: ellipsis;
}

.search{
    width: 200px;
}

@media all and (max-width: 750px) {
    .LegendDisplay{
        border: 1px solid #ededed;
        display: flex !important;
        justify-content: space-evenly;
        max-height: 150px;
        width: 95%;
        margin-top: 20px;
        overflow-x: hidden;
    }

    .countryNameList, .legendTitle, .search{
        width: 170px;
        max-width: 48vw;
        overflow: hidden;
    }
}

hr{
    margin-left: 10%;
    margin-right: 10%;
}

.search > input{
    width: 90%;
    margin: 0 10px 10px 10px;
}

.dot{
    color: white;
    fill:white;
}


.content{
    background-color:white;
}

.content>div{
    max-width:1200px;
    margin-left: auto;
    margin-right:auto;
}

.Chart-Wrapper{
    display:flex;
    width: 100%;
}

.Chart-Wrapper>div{
    display:flex;
    flex-flow: row wrap;
    align-items:top;
    margin: auto;
}

.Chart-Wrapper>div>div{
    display:inline-block;
    
}

.chart-content>*{
    background-color:white;
}

div.tooltip {
    background: white;
    box-shadow: 1px 1px 3px grey;
    height: auto;
    font: 12px sans-serif;
    position: absolute;
    width: 100px;
    padding: 5px;
    pointer-events: none;
}

.domain{
    stroke-width: 1.5px;
    color: grey;
}

text {
    fill: #343d47;
    color: #343d47;
}

.selectDropdown{
    margin:5px;
    padding: 2px;
    float: left;
}

.Footer, .Header{
    max-width: 1200px;
    margin: 15px auto 15px auto;
    padding-left:10px;
    position: relative;
}

.Footer{
    margin-top: 10px;
    font-size: 0.9em;
}

.headerWrapper{
    margin: 10px 0 10px 0;
}

h1, h2{
    margin: 0px 0px 0px 0px;
}

h2 {
    -webkit-text-decoration: italic;
            text-decoration: italic; 
    font-weight: 500;
}

.shareBttn{
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px;
    font-size: 1em;
    color: black;
    background-color: rgb(254, 214, 0);
    border-radius: 4px;
    border-color: none;
}

.shareBttn:focus{
    outline:none;
}

.noCopy{
    transition: 0.5s ease-in;
    opacity: 0;
}

.copied{
    background-color: rgb(254, 214, 0);
    border: 1px solid #f2f2f2;
    border-radius: 6px;
    color: #161616;
    left: 40%;
    font-size:1em;
    font-weight:500;
    padding: 10px;
    position: absolute;
    top: 10px;
    right: 50%;
    width: 300px;
    text-align: center;
}
