.Footer, .Header{
    max-width: 1200px;
    margin: 15px auto 15px auto;
    padding-left:10px;
    position: relative;
}

.Footer{
    margin-top: 10px;
    font-size: 0.9em;
}

.headerWrapper{
    margin: 10px 0 10px 0;
}

h1, h2{
    margin: 0px 0px 0px 0px;
}

h2 {
    text-decoration: italic; 
    font-weight: 500;
}

.shareBttn{
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px;
    font-size: 1em;
    color: black;
    background-color: rgb(254, 214, 0);
    border-radius: 4px;
    border-color: none;
}

.shareBttn:focus{
    outline:none;
}

.noCopy{
    transition: 0.5s ease-in;
    opacity: 0;
}

.copied{
    background-color: rgb(254, 214, 0);
    border: 1px solid #f2f2f2;
    border-radius: 6px;
    color: #161616;
    left: 40%;
    font-size:1em;
    font-weight:500;
    padding: 10px;
    position: absolute;
    top: 10px;
    right: 50%;
    width: 300px;
    text-align: center;
}